import React, { useEffect, useState } from "react"
import { Text, Flex, IconButton } from "@chakra-ui/react"
import moment from "moment"
import 'moment/locale/zh-tw'
import { SettingsIcon } from '@chakra-ui/icons'
import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverHeader,
  PopoverBody,
  PopoverArrow,
  PopoverCloseButton,
} from '@chakra-ui/react'


const IndexPage = () => {
  const [value, setValue] = useState(moment().format('YYYY/MM/DD dddd HH:mm:ss'));
  const [textColor, setTextColor] = useState("#000000");
  const [bgColor, setBgColor] = useState("#FFFFFF");

  useEffect(() => {
    const interval = setInterval(
      () => setValue(moment().format('YYYY/MM/DD dddd HH:mm:ss')),
      1000
    );

    return () => {
      clearInterval(interval);
    }
  }, []);

  return (
    <Flex direction="column">
      <Flex w="100%" h={window.innerHeight} bgColor={bgColor} align='center' justify='center'>
        <Text fontSize="120px" color={textColor}>{value}&nbsp;</Text>
        <Popover>
          <PopoverTrigger>
            <IconButton colorScheme='none' icon={<SettingsIcon color={textColor}/>} />
          </PopoverTrigger>
          <PopoverContent>
            <PopoverArrow />
            <PopoverCloseButton />
            <PopoverHeader>Settings</PopoverHeader>
            <PopoverBody>
              <Flex>
                <label>Text Color:
                  <input type='color' value={textColor} onChange={e => setTextColor(e.target.value)} placeholder='Text Color'></input></label>
                <label>Background Color:
                  <input type='color' value={bgColor} onChange={e => setBgColor(e.target.value)}></input></label>
              </Flex>
            </PopoverBody>
          </PopoverContent>
        </Popover>
      </Flex>
    </Flex>
  )
}

export default IndexPage
